.analyticPageWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  margin: auto;
}

.analyticsData {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.row {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 15px;
}

.item {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page {
  list-style: none;
}

.page:hover {
  cursor: pointer;
}

.button:hover {
  cursor: pointer;
}

.active {
  pointer-events:none;
  opacity: .6;
}

.headerWrapper {
  width: 100%;
  padding: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header {
  width: 100%;
  display: flex;
  justify-content: center;
}

.notActive {
  cursor: default !important;
}

.header:hover {
  cursor: pointer;
} 

.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  background: blue;
}

.button {
  height: 44px;
  width: 88px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background: blue;
  list-style: none;
}
